body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

$designWidth: 375;

@function px2rem($px:18) {
    @return $px*10/$designWidth + rem;
}

@mixin placeholder($placeholderColor) {
    &::-webkit-input-placeholder { /* WebKit browsers */
        color: $placeholderColor;
    }
    &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color: $placeholderColor;
        opacity: 1;
    }
    &::-moz-placeholder { /* Mozilla Firefox 19+ */
        color: $placeholderColor;
        opacity: 1;
    }
    &:-ms-input-placeholder { /* Internet Explorer 10+ */
        color: $placeholderColor;
    }
}
@font-face {
    font-family: 'iconfont';
    /* project id 1355540 */
    src: url('//at.alicdn.com/t/font_1355540_70bg8jcecbg.eot');
    src: url('//at.alicdn.com/t/font_1355540_70bg8jcecbg.eot?#iefix') format('embedded-opentype'),
        url('//at.alicdn.com/t/font_1355540_70bg8jcecbg.woff2') format('woff2'),
        url('//at.alicdn.com/t/font_1355540_70bg8jcecbg.woff') format('woff'),
        url('//at.alicdn.com/t/font_1355540_70bg8jcecbg.ttf') format('truetype'),
        url('//at.alicdn.com/t/font_1355540_70bg8jcecbg.svg#iconfont') format('svg');
}
.iconfont {
    font-family: 'iconfont'!important;
    font-size: px2rem(28);
    font-style:normal;
}

* {
    -webkit-appearance: none;
    box-sizing: border-box;
}
.small {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
}
.bigsmall {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
}
blockquote,
body,
button,
code,
dd,
div,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
legend,
li,
ol,
p,
pre,
td,
textarea,
th,
ul,
section {
    margin: 0;
    padding: 0;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

fieldset,
img,
button {
    border: none;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
    font-style: normal;
    font-weight: 400;
}

ol,
ul {
    list-style: none;
}

li {
    list-style: none;
}

q:after,
q:before {
    content: '';
}

button,
input,
select,
textarea {
    font: 100% "Microsoft YaHei", tahoma, arial, 'Hiragino Sans GB', '宋体', sans-serif;
}

a {
    text-decoration: none;
}

input::-ms-clear {
    display: none;
}

input::-ms-reveal {
    display: none;
}

input:focus {
    outline: none;
}

body,
html {
    height: 100%;
    overflow: hidden;
}


img,
span,
input {
    display: inline-block;
    *display: inline;
    *zoom: 1;
}

.sp-span span {
    display: inline;
}

button {
    background-color: transparent;
    outline: none;
}

#root {
    width: 100%;
    height: 100%;
    background: #fff;
}
